@import "variables";
@import "App";
@import "duties";
@import "review";
@import "calendar";
@import "sw";

svg.icon {
  width: 1em;
}

svg.reflect {
  -moz-transform: scaleX(-1);    /* Gecko */
  -o-transform: scaleX(-1);      /* Opera */
  -webkit-transform: scaleX(-1); /* Webkit */
  transform: scaleX(-1);         /* Standard */
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: sans-serif;
}

hr {
  display: block;
  overflow: hidden;
  border-style: inset;
  border-width: 1px;
}
